import { mapState } from 'vuex' 
import { upImgUrl, _shop_type } from '../../libs/envLib'
export default {
    data() {
        return {
            ticketCode: '',
            ticketPassword: '',
            imgUploading: false,
            headerImg: '',
            forbidClick: false,
            name: '',
            ticketName: '',
            showScanBtn: true
        }
    },
    filters: {
        phoneText(val) {
            let arr = val.split('')
            return `${arr.splice(0,3).join('')}****${arr.splice(4,4).join('')}`
        }
    },
    onLoad(option) {
        console.log(option)
        this.ticketCode = option.code || ''
        this.ticketPassword = option.password || ''
        this.ticketName = option.name || ''
        if (option.code) {
            this.showScanBtn = false
        }
    },
    computed: {        
        ...mapState(['ticketBaseInfo','userInfo']),
    },
    methods: {
        handlerCodeBlur (e) {
            this.ticketCode = e.target.value
        },
        handlerPassword (e) {
            this.ticketPassword = e.target.value
        },
        bindTicket() {
            if (this.forbidClick) return
            if (!this.checkForm()) return
            this.forbidClick = true
            setTimeout(() => {
                this.sendData()
            },200)            
        },
        scanBind () {    
            let that = this        
            uni.scanCode({
                onlyFromCamera: true,
                scanType: 'QR_CODE',
                success (res) {
                    that.postScanData(res.result)
                },
                fail () {
                    that.$tip.toast('扫描失败,请重试','none')
                }
            })
        },
        postScanData (val) {
            let temp = null
            console.log(val)
            try {
                temp = JSON.parse(val)
            } catch (error) {
                this.$tip.alertDialog('请扫描正确的联票二维码')
                return
            }
            if (temp && temp.isLEXING) {
                this.ticketCode = temp.ticketCode
                this.ticketPassword = temp.ticketPassword
                    this.$tip.toast('联票添加成功','none')
            } else {                
                this.$tip.alertDialog(`非${_shop_type === 'lx' ? '乐行' : '欢游'}公司发行的联票`)
            }
        },
        async sendData () {
            let params = {
                bindingName: this.name,
                bingingPhoto: this.headerImg,
                childCode : this.ticketCode,
                password : this.ticketPassword,
                username: this.userInfo.username
            }
            const res = await this.$api.bindTicket(params)
            if (res.code === '0') {
                this.$tip.toast('绑定成功','none')                
                uni.navigateBack({})
            } else {
                this.forbidClick = false
                this.$tip.toast(res.message,'none')
            }
        },
        checkForm () {
            if (this.name === '') {
                this.$tip.toast('请输入使用者名称','none')
                return false
            }
            if (this.headerImg === '') {
                this.$tip.toast('请上传头像','none')
                return false
            }
            if (this.ticketCode === '') {
                this.$tip.toast('请输入联票编号','none')
                return false
            }
            if (this.ticketPassword === '') {
                this.$tip.toast('请输入联票密码','none')
                return false
            }
            if (this.imgUploading) {
                this.$tip.toast('请等待头像上传完成','none')
                return false
            }
            return true
        },
        showWarning () {
            this.$tip.confirm('上传单人大头照，仅限照片本人使用，合照不可使用')
            .then((res) => {
                this.uploadImg()
            })
            .catch(() => {
                return
            })
        },
        uploadImg () {
            let that = this
            wx.chooseImage({
                count: 1,
                sizeType: ['compressed'],
                sourceType: ['album','camera'],
                success (res) {
                    const tempFilePaths = res.tempFilePaths
                    that.imgUploading = true
                    console.log(res)
                    wx.uploadFile({
                        url: upImgUrl,
                        filePath: tempFilePaths[0],
                        name: 'file',
                        success (res1) {
                            let temp = JSON.parse(res1.data)
                            if (temp.code === '0') {  
                                that.headerImg = temp.data
                                that.$tip.toast('头像上传成功','none')
                            } else {
                                that.$tip.toast(temp.message,'none')
                            }
                        },
                        complete () {
                            setTimeout(() => {                                        
                                that.imgUploading = false
                            }, 1000);                    
                        }
                    })
                }
            })
        }
    }
}
